.AutoCompleteText{
    width: 100%;
    border: 1px solid grey;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, .1), 
                0 2px 4px 1px rgba(0, 0, 0, .18);
    color: rgba(0, 0, 0, 0.73);
    font-family: Arial, Helvetica, sans-serif;
    font-size: 14px;
    position: absolute;
    z-index: 10;
    background-color: rgba(255, 255, 255, 0.9);
}

.AutoCompleteText input {
    width: 100%;
    border: none;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.73);
    padding: 10px 5px;
    box-sizing: border-box;
}

.AutoCompleteText ul {
    list-style-type: none;
    text-align: left;
    margin: 0;
    padding: 0;
    border-top: 1px solid gray;
}

.AutoCompleteText ul::before{
    content: "";
    cursor: pointer;
}

.AutoCompleteText li {
    padding: 10px 5px;
}

.AutoCompleteText li:hover{
    background-color: rgba(128, 128, 128, 0.20);
}

.media_producto { 
    min-width: 50px;
    max-width: 75px;
    min-height: 100px;
    max-height: 140px;
}

.infoProducto div {    
    margin: 3px;
}