.media_producto {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
}

.productoItem {
    list-style: none;
    padding-bottom: 10px;
    text-align: center;
}

.imagenProducto {
    min-width: 110px;
    max-width: 150px;
    min-height: 200px;
    max-height: 250px;
}

.cardImagen {
    height: 260px;
}

.iconoEditar {
    min-width: 30px;
    min-height: 30px;
    margin-top: -33px;
    margin-left: 229px;
    position: absolute;
    float: right;
    cursor: pointer;
}

.cardProducto {
    max-height: 600px;
}

.infoProducto div {    
    margin: 4px;
}

.nombreProducto {
    font-size: 15px;
}

.btn-circle {
    width: 50px;
    height: 50px;
    line-height: 55px;
    border-radius: 50% !important;
}
